body {
  font-family: "Space Mono", monospace;
  font-size: 1.8rem;
  background-color: hsl(185deg, 41%, 84%);
}

p {
  line-height: 1.7;
  margin: 0;
}

.contenedor {
  max-width: 92rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

button {
  border: none;
  background-color: transparent;
}
button:focus {
  outline: 0.3rem dashed hsl(172deg, 67%, 45%);
}
button:hover {
  cursor: pointer;
}

input {
  height: 4rem;
  background-color: hsl(189deg, 41%, 97%);
  border-color: transparent;
  border-radius: 1rem;
  padding: 2rem;
  text-align: right;
  color: hsl(183deg, 100%, 15%);
}
@media (min-width: 992px) {
  input {
    font-size: 2.4rem;
    height: 4.2rem;
  }
}
input::placeholder {
  color: hsl(184deg, 14%, 56%);
}
input:focus {
  outline: none;
}
input:hover {
  border: 0.2rem solid hsl(172deg, 67%, 45%);
}

.app {
  margin-top: 7.5rem;
}
@media (min-width: 450px) {
  .app {
    margin-bottom: 4rem;
  }
}
.app img {
  margin: 0 auto;
}
.app .calculadora-grid {
  max-width: 45rem;
  margin-top: 4rem;
  margin-inline: auto;
  background-color: #fff;
  border-radius: 2rem 2rem 0 0;
  padding: 3rem;
  display: grid;
  gap: 3.5rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 450px) {
  .app .calculadora-grid {
    border-radius: 2rem;
  }
}
@media (min-width: 992px) {
  .app .calculadora-grid {
    display: grid;
    gap: 3.5rem;
    grid-template-columns: repeat(2, 1fr);
    max-width: 100%;
    margin-top: 6rem;
    padding: 5rem;
  }
}
.app .calculadora-grid .calculadora {
  display: flex;
  flex-direction: column;
  gap: 3.5rem;
}
.app .calculadora-grid .calculadora .cuenta,
.app .calculadora-grid .calculadora .propina-btns,
.app .calculadora-grid .calculadora .numero-personas {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}
.app .calculadora-grid .calculadora .cuenta label,
.app .calculadora-grid .calculadora .propina-btns label,
.app .calculadora-grid .calculadora .numero-personas label {
  color: hsl(184deg, 14%, 56%);
  display: flex;
  justify-content: space-between;
}
.app .calculadora-grid .calculadora .input-cuenta,
.app .calculadora-grid .calculadora .input-personas {
  background-image: url(../img/icon-dollar.svg);
  background-repeat: no-repeat;
  background-position: 2rem center;
}
.app .calculadora-grid .calculadora .input-cuenta.error,
.app .calculadora-grid .calculadora .input-personas.error {
  border: 0.2rem solid red;
}
.app .calculadora-grid .calculadora .input-personas {
  background-image: url(../img/icon-person.svg);
}
.app .calculadora-grid .calculadora .input-custom {
  height: 5rem;
  width: 100%;
}
.app .calculadora-grid .calculadora .input-custom::placeholder {
  color: hsl(186deg, 14%, 43%);
  font-size: 2rem;
  text-align: left;
}
.app .calculadora-grid .calculadora .input-custom.error {
  border: 0.2rem solid red;
}
.app .calculadora-grid .calculadora .grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 992px) {
  .app .calculadora-grid .calculadora .grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.app .calculadora-grid .calculadora .grid button {
  background-color: hsl(183deg, 100%, 15%);
  height: 5rem;
  color: #fff;
  font-size: 2.2rem;
  border-radius: 1rem;
  transition: all 0.3s;
  letter-spacing: 0.1rem;
}
.app .calculadora-grid .calculadora .grid button:hover {
  color: hsl(183deg, 100%, 15%);
  background-color: #a1ece2;
}
.app .calculadora-grid .calculadora .grid button.activo {
  background-color: hsl(172deg, 67%, 45%);
  color: hsl(183deg, 100%, 15%);
}
.app .calculadora-grid .calculadora .errorMensaje {
  color: red;
}
.app .calculadora-grid .resultado {
  background-color: hsl(183deg, 100%, 15%);
  border-radius: 2rem;
  padding: 3rem 2rem;
}
@media (min-width: 992px) {
  .app .calculadora-grid .resultado {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 4rem 3rem;
  }
}
.app .calculadora-grid .resultado .propina-persona,
.app .calculadora-grid .resultado .total-persona {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.app .calculadora-grid .resultado .propina-persona div,
.app .calculadora-grid .resultado .total-persona div {
  display: flex;
  align-items: center;
  gap: 0.4rem;
}
.app .calculadora-grid .resultado .propina-persona p,
.app .calculadora-grid .resultado .total-persona p {
  color: #fff;
}
.app .calculadora-grid .resultado .propina-persona p span,
.app .calculadora-grid .resultado .total-persona p span {
  display: block;
  font-size: 1.5rem;
  color: hsl(184deg, 14%, 56%);
}
.app .calculadora-grid .resultado .propina-persona .cantidad,
.app .calculadora-grid .resultado .total-persona .cantidad {
  color: hsl(172deg, 67%, 45%);
  font-size: 2.8rem;
  display: flex;
  align-items: center;
  gap: 0.2rem;
}
@media (min-width: 992px) {
  .app .calculadora-grid .resultado .propina-persona .cantidad,
.app .calculadora-grid .resultado .total-persona .cantidad {
    font-size: 4.2rem;
  }
}
.app .calculadora-grid .resultado .propina-persona {
  margin-bottom: 3rem;
}
.app .calculadora-grid .resultado .btn-reset {
  margin-top: 3rem;
  width: 100%;
  height: 5rem;
  text-align: center;
  color: hsl(183deg, 100%, 15%);
  background-color: hsl(172deg, 67%, 45%);
  border-radius: 0.5rem;
  font-size: 2rem;
  transition: all 0.3s;
}
.app .calculadora-grid .resultado .btn-reset:hover {
  background-color: hsl(189deg, 41%, 97%);
}
.app .calculadora-grid .resultado .btn-reset:focus {
  outline: 0.3rem dashed #fff;
}
.app .calculadora-grid .resultado .btn-reset.deshabilitado {
  opacity: 0.4;
}